* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

.app__general__container {
  background-color: lightgrey;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.app__card__container {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 10px 10px 20px 1px #888888;
  margin: 20px;
}

.gitHub__filter--container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.gitHub__filter--container .filter--field {
  width: 360px;
  font-size: 18px;
  padding: 10px;
  border-radius: 5px;
  margin-right: 10px;
  color: grey;
}

.gitHub__filter--container .filter--button {
  font-size: 18px;
  font-weight: bold;
  padding: 10px;
  border-radius: 5px;
  background-color: #ece7e7;
}

.user--container {
  display: flex;
  align-items: center;
  margin: 15px 0px 5px 0px;
}

.user--container .user--card {
  margin-left: 20px;
}

.user--container .user--card .user--userName {
  font-style: italic;
}

.fab.fa-github-square {
  font-size: 100px;
}

.repos--title {
  border-bottom: solid 1px black;
  line-height: 40px;
}

.user--repos__list {
  list-style: none;
  padding: 10px;
}

.repos__list--item {
  font-weight: bold;
  border-bottom: solid 0.5px grey;
  margin: 5px;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.repos__list--item .repos__list--link {
  text-decoration: none;
  color: black;
}

.list--puntuation .item--puntuation {
  font-size: 12px;
  list-style: none;
}

.add--container {
  background-color: lightcoral;
  padding: 10px;
  width: 400px;
  color: darkred;
  border: solid 0.5px darkred;
  border-radius: 5px;
}
